import appConfig from '@/../app.config'
import InjectedConnector from '@/plugins/web3/connectors/injected'
import BscConnector from '@/plugins/web3/connectors/binance_chain_wallet'

const chainId = appConfig.contract.networks[appConfig.contract.currentNetwork].chainId

const bscConnector = new BscConnector({ supportedChainIds: [chainId] })
const injectedConnector = new InjectedConnector({ supportedChainIds: [chainId] })

export const ConnectorNames = {
    Injected: 'injected',
    BSC: 'bsc',
}

export const Connectors = [
    {
        title: 'Metamask',
        icon: 'metamask',
        connectorId: ConnectorNames.Injected,
    },
    {
        title: 'TrustWallet',
        icon: 'trust_wallet',
        connectorId: ConnectorNames.Injected,
    },
    {
        title: 'Binance Chain Wallet',
        icon: 'binance_chain',
        connectorId: ConnectorNames.BSC,
    },
]

export const ConnectorsByName = {
    [ConnectorNames.Injected]: injectedConnector,
    [ConnectorNames.BSC]: bscConnector,
}
