import { EventEmitter } from 'events'

export const ConnectorEvent = {
    Update: 'Web3ReactUpdate',
    Error: 'Web3ReactError',
    Deactivate: 'Web3ReactDeactivate'
}

export class AbstractConnector extends EventEmitter {
    constructor({ supportedChainIds }) {
        super()
        this.supportedChainIds = supportedChainIds
    }

    async activate() {
        throw new Error('Abstract method!')
    }
    async getProvider() {
        throw new Error('Abstract method!')
    }
    async getChainId() {
        throw new Error('Abstract method!')
    }
    async getAccount() {
        throw new Error('Abstract method!')
    }
    deactivate() {
        throw new Error('Abstract method!')
    }

    emitUpdate(update) {
        this.emit(ConnectorEvent.Update, update)
    }

    emitError(error) {
        this.emit(ConnectorEvent.Error, error)
    }

    emitDeactivate() {
        this.emit(ConnectorEvent.Deactivate)
    }
}
