<template>
    <div class="lang dropdown" :class="{ opened: open }">
        <button class="dropdown-open" aria-label="language" @click="toggle">
            <img class="flag" :src="`/flags/${active_lang.code}.png`" alt="" />
            <span> {{ active_lang.name }} </span>
            <span class="icon-pre-arrow-down"></span>
        </button>
        <div class="lang-items dropdown-items" v-if="open">
            <a v-for="lang in available_langs" :key="lang.code" href="" class="dropdown-item" @click.prevent="selectLang(lang)">
                <img :src="`/flags/${lang.code}.png`" alt="" class="flag" />
                <span>{{ lang.name }}</span>
            </a>
        </div>
    </div>
</template>

<script>
export default {
    name: 'LanguageSwitcher',
    data() {
        return {
            open: false,
        }
    },
    created() {
        document.body.addEventListener('click', this.onOtherClick)
    },
    computed: {
        active_lang() {
            return this.available_langs.find(lang => lang.code === this.$i18n.locale)
        },
        available_langs() {
            return this.$i18n.availableLocales.map(code => {
                return {
                    code: code,
                    name: this.$t('_locale_name', code)
                }
            })
        }
    },
    methods: {
        onOtherClick(e) {
            const path = e.path || (e.composedPath && e.composedPath())
            if (!path.includes(this.$el.querySelector('.dropdown-open'))) this.close()
        },
        close() {
            this.open = false
        },
        toggle() {
            this.open = !this.open
        },
        selectLang(lang) {
            this.$i18n.locale = lang.code
        }
    },
}
</script>

