import { AbstractConnector } from '../abstract-connector'

export class NoBscProviderError extends Error {
    constructor() {
        super()
        this.name = this.constructor.name
        this.message = 'No BSC provider was found on window.BinanceChain.'
    }
}

export class UserRejectedRequestError extends Error {
    constructor() {
        super()
        this.name = this.constructor.name
        this.message = 'The user rejected the request.'
    }
}

function parseSendReturn(sendReturn) {
    // eslint-disable-next-line no-prototype-builtins
    return sendReturn.hasOwnProperty('result') ? sendReturn.result : sendReturn
}

export default class BscConnector extends AbstractConnector {
    constructor(kwargs) {
        super(kwargs)

        this.handleNetworkChanged = this.handleNetworkChanged.bind(this)
        this.handleChainChanged = this.handleChainChanged.bind(this)
        this.handleAccountsChanged = this.handleAccountsChanged.bind(this)
        this.handleClose = this.handleClose.bind(this)
    }

    handleChainChanged(chainId = '') {
        this.emitUpdate({ chainId, provider: window.BinanceChain })
        // console.log('emitUpdate', { chainId, provider: window.BinanceChain })
    }
    handleAccountsChanged(accounts = []) {
        if (accounts.length === 0) {
            this.emitDeactivate()
            // console.log('emitDeactivate')
        } else {
            this.emitUpdate({ account: accounts[0] })
            // console.log('emitUpdate', { account: accounts[0] })
        }
    }
    handleClose() {
        this.emitDeactivate()
        // console.log('emitDeactivate')
    }
    handleNetworkChanged(networkId = '') {
        this.emitUpdate({ chainId: networkId, provider: window.BinanceChain })
        // console.log('emitUpdate', { chainId: networkId, provider: window.BinanceChain })
    }

    async getProvider() {
        return window.BinanceChain
    }

    async activate() {
        if (!window.BinanceChain) {
            throw new NoBscProviderError()
        }

        if (window.BinanceChain.on) {
            window.BinanceChain.on('chainChanged', this.handleChainChanged)
            window.BinanceChain.on('accountsChanged', this.handleAccountsChanged)
            window.BinanceChain.on('close', this.handleClose)
            window.BinanceChain.on('networkChanged', this.handleNetworkChanged)
        }

        if (window.BinanceChain.isMetaMask) {
            window.BinanceChain.autoRefreshOnNetworkChange = false
        }

        // try to activate + get account via eth_requestAccounts
        let account
        try {
            account = await window.BinanceChain.send('eth_requestAccounts').then(
                sendReturn => parseSendReturn(sendReturn)[0]
            )
        } catch (error) {
            if (error.code === 4001) {
                throw new UserRejectedRequestError()
            }
            console.warn('eth_requestAccounts was unsuccessful, falling back to enable')
        }

        // if unsuccessful, try enable
        if (!account) {
            // if enable is successful but doesn't return accounts, fall back to getAccount (not happy i have to do this...)
            account = await window.BinanceChain.enable().then(
                sendReturn => sendReturn && parseSendReturn(sendReturn)[0]
            )
        }

        return { provider: window.BinanceChain, ...(account ? { account } : {}) }
    }

    async getChainId() {
        if (!window.BinanceChain) {
            throw new NoBscProviderError()
        }

        let chainId
        try {
            chainId = await window.BinanceChain.send('eth_chainId').then(parseSendReturn)
        } catch {
            console.warn('eth_chainId was unsuccessful, falling back to net_version')
        }

        if (!chainId) {
            try {
                chainId = await window.BinanceChain.send('net_version').then(parseSendReturn)
            } catch {
                console.warn('net_version was unsuccessful, falling back to net version v2')
            }
        }

        if (!chainId) {
            try {
                chainId = parseSendReturn(window.BinanceChain.send({ method: 'net_version' }))
            } catch {
                console.warn('net_version v2 was unsuccessful, falling back to manual matches and static properties')
            }
        }

        if (!chainId) {
            if (window.BinanceChain.isDapper) {
                chainId = parseSendReturn(window.BinanceChain.cachedResults.net_version)
            } else {
                chainId =
                    window.BinanceChain.chainId ||
                    window.BinanceChain.netVersion ||
                    window.BinanceChain.networkVersion ||
                    window.BinanceChain._chainId
            }
        }

        return chainId
    }

    async getAccount() {
        if (!window.BinanceChain) {
            throw new NoBscProviderError()
        }

        let account
        try {
            account = await window.BinanceChain.send('eth_accounts').then(sendReturn => parseSendReturn(sendReturn)[0])
        } catch {
            console.warn('eth_accounts was unsuccessful, falling back to enable')
        }

        if (!account) {
            try {
                account = await window.BinanceChain.enable().then(sendReturn => parseSendReturn(sendReturn)[0])
            } catch {
                console.warn('enable was unsuccessful, falling back to eth_accounts v2')
            }
        }

        if (!account) {
            account = parseSendReturn(window.BinanceChain.send({ method: 'eth_accounts' }))[0]
        }

        return account
    }

    deactivate() {
        if (window.BinanceChain && window.BinanceChain.removeListener) {
            window.BinanceChain.removeListener('chainChanged', this.handleChainChanged)
            window.BinanceChain.removeListener('accountsChanged', this.handleAccountsChanged)
            window.BinanceChain.removeListener('close', this.handleClose)
            window.BinanceChain.removeListener('networkChanged', this.handleNetworkChanged)
        }
    }

    async isAuthorized() {
        if (!window.BinanceChain) {
            return false
        }

        try {
            return await window.BinanceChain.send('eth_accounts').then(sendReturn => {
                return parseSendReturn(sendReturn).length > 0
            })
        } catch {
            return false
        }
    }
}
