<template>
    <button class="btn bold" :class="className" :disabled="isBusy" :type="type">
        <spinner v-if="isBusy" size="15" :speed="0.4" style="display: inline-block" />
        <slot v-else>Click</slot>
    </button>
</template>

<script>
export default {
    name: 'v-button',
    props: {
        isBusy: {
            type: Boolean,
            default: false,
        },
        className: {
            type: String,
            default: '',
        },
        type: {
            type: String,
            default: 'button',
        },
    },
}
</script>

