<template>
    <div v-if="value" class="modal connect-modal">
        <div class="shadow" @click="close"></div>
        <div class="modal-dialog">
            <div class="modal-content">
                <div class="modal-title medium">
                    {{ $t('connect_modal.title') }}
                    <span class="icon-close" @click="close"></span>
                </div>
                <div class="list">
                    <div
                        v-for="connector in connectors"
                        :key="connector.title"
                        @click="connect(connector)"
                        class="item medium"
                    >
                        {{ connector.title }}
                        <img :src="`/img/connectors/${connector.icon}.png`" alt="Icon" />
                    </div>
                </div>
                <div class="learn yellow">
                    <span class="icon-questions"></span>
                    {{ $t('connect_modal.learn') }}
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { Connectors } from '@/connectors'

export default {
    name: 'ConnectModal',
    props: {
        value: {
            type: Boolean,
            default: false,
        },
    },
    computed: {
        connectors() {
            return Connectors
        },
    },
    watch: {
        value(n) {
            if (n) {
                document.querySelector('body').classList.add('modal-opened')
            } else {
                document.querySelector('body').classList.remove('modal-opened')
            }
        },
    },
    methods: {
        close() {
            this.$emit('input', false)
        },
        async connect(connector) {
            this.close()

            try {
                await this.$store.dispatch('authLogin', connector.connectorId)
            } catch (error) {
                this.$root.makeErrorToast(error.message, error.title)
            }
        },
    },
}
</script>

<style></style>
