import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store'
import Index from '@/views/Index.vue'
import Account from '@/views/Account.vue'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'Index',
        component: Index,
    },
    {
        path: '/account',
        name: 'Account',
        component: Account,
        meta: {
            require_auth: true,
        },
    },
]

const router = new VueRouter({
    mode: 'hash',
    routes,
    scrollBehavior(to, from) {
        if (!to.query.to && to.path !== from.path) return { x: 0, y: 0 }
    },
})

router.beforeEach((to, from, next) => {
    if (to.meta && to.meta.require_auth && !store.getters.is_logged_in) {
        return next({
            name: 'Index',
        })
    }

    return next()
})

export default router
