import numeral from 'numeral'
import 'numeral/locales/en-gb'

numeral.locale('en-gb')

const formatNumber = (value, format) => {
    if (format.startsWith('0 0')) {
        return (
            numeral(value)
                // .format('0,0')
                .format(format.replace(/^0 0/, '0,0'))
                .split(',')
                .join(' ')
        )
    }

    return numeral(value).format(format)
}

export default {
    install(_Vue) {
        _Vue.prototype.$formatNumber = formatNumber
        _Vue.filter('formatNumber', formatNumber)
    }
}