import { NoEthereumProviderError, UserRejectedRequestError } from '@/plugins/web3/connectors/injected'
import { NoBscProviderError } from '@/plugins/web3/connectors/binance_chain_wallet'
import { UnsupportedChainIdError } from '@/plugins/web3'

export function getWeb3ErrorMessage(error) {
    if (error instanceof NoEthereumProviderError) {
        return 'No Ethereum browser extension detected, install MetaMask on desktop or visit from a dApp browser on mobile.'
    } else if (error instanceof NoBscProviderError) {
        return 'No Binance Chain Wallet browser extension detected.'
    } else if (error instanceof UnsupportedChainIdError) {
        return "You're connected to an unsupported network."
    } else if (error instanceof UserRejectedRequestError) {
        return 'Please authorize this website to access your Ethereum account.'
    } else {
        console.error(error)
        return 'An unknown error occurred. Check the console for more details.'
    }
}

export async function binanceChainListener() {
    new Promise((resolve) => {
        setTimeout(() => {
            resolve()
        }, 1000)

        Object.defineProperty(window, 'BinanceChain', {
            get() {
                return this.bsc
            },
            set(bsc) {
                this.bsc = bsc

                resolve()
            },
        })
    })
}

export function sleep(milliseconds) {
    return new Promise((resolve) => {
        setTimeout(resolve, milliseconds)
    })
}
