import Web3 from 'web3'
import { ConnectorEvent } from './connectors/abstract-connector'
import { normalizeChainId } from './normalizers'

export class UnsupportedChainIdError extends Error {
    constructor(unsupportedChainId, supportedChainIds = []) {
        super()
        this.name = this.constructor.name
        this.message = `Unsupported chain id: ${unsupportedChainId}. Supported chain ids are: ${supportedChainIds}.`
    }
}

const ActionType = {
    ACTIVATE_CONNECTOR: 0,
    UPDATE: 1,
    UPDATE_FROM_ERROR: 2,
    ERROR: 3,
    ERROR_FROM_ACTIVATION: 4,
    DEACTIVATE_CONNECTOR: 5,
}

function plugin(_Vue) {
    async function augmentConnectorUpdate(connector, update) {
        const provider = update.provider === undefined ? await connector.getProvider() : update.provider
        const [_chainId, _account] = await Promise.all([
            update.chainId === undefined ? connector.getChainId() : update.chainId,
            update.account === undefined ? connector.getAccount() : update.account,
        ])

        const chainId = normalizeChainId(_chainId)
        if (!!connector.supportedChainIds && !connector.supportedChainIds.includes(chainId)) {
            throw new UnsupportedChainIdError(chainId, connector.supportedChainIds)
        }
        const account = _account

        return { provider, chainId, account }
    }

    function updatedStateByAction(action, payload = {}) {
        const default_state = {
            // Try to set Browser given provider by default
            ...(this.library_.givenProvider ? { provider: this.library_.givenProvider } : {}),
        }

        switch (action) {
            case ActionType.ACTIVATE_CONNECTOR:
                {
                    const { connector, provider, chainId, account, onError } = payload
                    this.state_ = {
                        ...default_state,
                        ...{ connector, provider, chainId, account, onError },
                    }
                }
                break
            case ActionType.UPDATE:
                {
                    const { provider, chainId, account } = payload
                    this.state_ = {
                        ...default_state,
                        ...this.state_,
                        ...(provider === undefined ? {} : { provider }),
                        ...(chainId === undefined ? {} : { chainId }),
                        ...(account === undefined ? {} : { account }),
                    }
                }
                break
            case ActionType.UPDATE_FROM_ERROR:
                {
                    const { provider, chainId, account } = payload
                    this.state_ = {
                        ...default_state,
                        ...this.state_,
                        ...(provider === undefined ? {} : { provider }),
                        ...(chainId === undefined ? {} : { chainId }),
                        ...(account === undefined ? {} : { account }),
                        error: undefined,
                    }
                }
                break
            case ActionType.ERROR:
                {
                    const { error } = payload
                    const { connector, onError } = this.state_
                    this.state_ = {
                        ...default_state,
                        connector,
                        error,
                        onError,
                    }
                }
                break
            case ActionType.ERROR_FROM_ACTIVATION:
                {
                    const { connector, error } = payload
                    this.state_ = {
                        ...default_state,
                        connector,
                        error,
                    }
                }
                break
            case ActionType.DEACTIVATE_CONNECTOR:
                {
                    this.state_ = {
                        ...default_state,
                    }
                }
                break
        }
    }

    async function handleConnectorUpdate(update) {
        const { connector, onError, error } = this.state

        // updates are handled differently depending on whether the connector is active vs in an error state
        if (!error) {
            const chainId = update.chainId === undefined ? undefined : normalizeChainId(update.chainId)

            if (
                chainId !== undefined &&
                !!connector.supportedChainIds &&
                !connector.supportedChainIds.includes(chainId)
            ) {
                const error = new UnsupportedChainIdError(chainId, connector.supportedChainIds)
                onError
                    ? onError(error)
                    : updatedStateByAction.call(this, ActionType.ERROR, {
                          error,
                      })
            } else {
                const account = update.account
                updatedStateByAction.call(this, ActionType.UPDATE, {
                    provider: update.provider,
                    chainId,
                    account,
                })
            }
        } else {
            try {
                const augmentedUpdate = await augmentConnectorUpdate(connector, update)

                updatedStateByAction.call(this, ActionType.UPDATE_FROM_ERROR, {
                    connector,
                    ...augmentedUpdate,
                })
            } catch (error) {
                onError
                    ? onError(error)
                    : updatedStateByAction.call(this, ActionType.ERROR, {
                          error,
                      })
            }
        }
    }

    async function handleConnectorError(error) {
        const { onError } = this.state
        onError ? onError(error) : updatedStateByAction.call(this, ActionType.ERROR, { error })
    }
    async function handleConnectorDeactivate() {
        if (this.state.connector) {
            this.state.connector.deactivate()
        }
    }

    function subscribeConnectorEvents() {
        if (this.state.connector)
            this.state.connector
                .on(ConnectorEvent.Update, handleConnectorUpdate.bind(this))
                .on(ConnectorEvent.Error, handleConnectorError.bind(this))
                .on(ConnectorEvent.Deactivate, handleConnectorDeactivate.bind(this))
    }
    function unsubscribeConnectorEvents() {
        if (this.state.connector)
            this.state.connector
                .off(ConnectorEvent.Update, handleConnectorUpdate.bind(this))
                .off(ConnectorEvent.Error, handleConnectorError.bind(this))
                .off(ConnectorEvent.Deactivate, handleConnectorDeactivate.bind(this))
    }

    function VueWeb3(options) {
        return new _Vue({
            data: {
                library_: undefined,
                contract_: undefined,
                state_: {},
            },
            computed: {
                library() {
                    return this.library_
                },
                contract() {
                    return this.contract_
                },
                state() {
                    return { ...this.state_ }
                },
                isActive() {
                    return (
                        this.state_.connector !== undefined &&
                        this.state_.chainId !== undefined &&
                        this.state_.account !== undefined &&
                        !this.state_.error
                    )
                },
            },
            watch: {
                'state_.provider'(provider) {
                    this.library_.setProvider(provider)
                },
                'state_.connector'(connector, oldConnector) {
                    if (connector) {
                        subscribeConnectorEvents.call(this)
                    } else {
                        oldConnector && oldConnector.deactivate()
                        unsubscribeConnectorEvents.call(this)
                    }
                },
            },
            created() {
                this.library_ = new Web3()

                if (!this.library_.givenProvider && options.fallbackProvider) {
                    this.library_.givenProvider = options.fallbackProvider
                }

                if (options.web3) {
                    this.library_.eth.handleRevert = options.web3.handleRevert || false
                }

                if (options.contract) this.initContract(options.contract.abi, options.contract.address)

                updatedStateByAction.call(this, ActionType.UPDATE)
            },
            methods: {
                initContract(abi, address) {
                    this.contract_ = new this.library_.eth.Contract(abi, address)
                },

                async activate(connector, onError, throwError = false) {
                    let activated = false

                    try {
                        const update = await connector.activate().then((update) => {
                            activated = true
                            return update
                        })

                        const augmentedUpdate = await augmentConnectorUpdate(connector, update)

                        updatedStateByAction.call(this, ActionType.ACTIVATE_CONNECTOR, {
                            connector,
                            ...augmentedUpdate,
                            onError,
                        })
                    } catch (error) {
                        if (throwError) {
                            activated && connector.deactivate()
                            throw error
                        } else if (typeof onError === 'function') {
                            activated && connector.deactivate()
                            onError(error)
                        } else {
                            updatedStateByAction.call(this, ActionType.ERROR_FROM_ACTIVATION, {
                                connector,
                                error,
                            })
                        }
                    }

                    return this.state
                },

                setError(error) {
                    updatedStateByAction.call(this, ActionType.ERROR, { error })
                },

                deactivate() {
                    updatedStateByAction.call(this, ActionType.DEACTIVATE_CONNECTOR)
                },
            },
            beforeDestroy() {
                this.state_.connector && this.state_.connector.deactivate()
                unsubscribeConnectorEvents.call(this)
            },
        })
    }

    return { VueWeb3 }
}

export default {
    install(_Vue, options = {}) {
        if (!options.contract || !options.contract.address || !options.contract.abi) {
            console.warn("[VueWeb3] Required 'contract' option is missing. Contract instance will be not created.")
        }

        const { VueWeb3 } = plugin(_Vue)
        const web3_instance = new VueWeb3(options)

        _Vue.prototype.$web3 = web3_instance

        if (options.store) {
            options.store.$web3 = web3_instance
        }
    },
}
