                                </div>
                            </div>
                            <div class="table-line text-regular">
                                <div class="table-text">{{ $t('account.basic_stat_section.table.line_3') }}</div>
                                <div class="table-value bold" :class="{ 'zero-value': isNaN(user.investments_count) }">
                                    {{
                                        !isNaN(user.investments_count)
                                            ? $formatNumber(user.investments_count, '0,0')
