                                    }}
                                </div>
                            </div>
                            <div class="table-line text-regular">
                                <div class="table-text">{{ $t('account.basic_stat_section.table.line_4') }}</div>
                                <div class="table-value bold">
                                    {{
                                        `${$formatNumber(user_investments_active.length, '0,0')}/${$formatNumber(
                                            user_active_investments_limit,
                                            '0,0'
                                        )}`
                                    }}
                                    <v-button
                                        v-if="user_investments_active.length < user_active_investments_limit"
                                        className="primary medium text-regular"
                                        @click.native="$router.replace({ query: { ...$route.query, to: 'invest' } })"
                                        :isBusy="false"
                                    >
                                        <span>{{ $t('_common.invest') }}</span>
                                    </v-button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="investment-page" ref="investments">
            <div class="container">
                <div class="title-wrap">
                    <div class="text-large extra_bold ttu account-title">
                        {{ $t('account.investment_section.section_title') }}
                    </div>
                    <div class="tabs">
                        <div
                            class="tab bold"
                            :class="{ active: active_tab === 'active' }"
                            @click="changeActiveTab('active')"
