                        </div>
                        <div
                            class="tab bold"
                            :class="{ active: active_tab === 'finished' }"
                            @click="changeActiveTab('finished')"
                        >
                            {{ $t('account.investment_section.tabs.finished') }} ({{ user_investments_expired.length }})
                        </div>
                        <div
                            class="tab bold"
                            :class="{ active: active_tab === 'history' }"
                            @click="changeActiveTab('history')"
                        >
                            {{ $t('account.investment_section.tabs.history') }} ({{ user_investments_history.length }})
                        </div>
                    </div>
                </div>
                <div v-if="loaders.investments" class="investment-items">
                    <h2 style="text-align: center">{{ $t('_common.loading') }}...</h2>
                </div>
                <div v-else class="investment-items">
                    <div v-if="active_tab === 'active'">
                        <InvestmentCard
                            v-for="investment in user_investments_active.slice(
                                0,
                                user_investments_active_pagination.page * user_investments_active_pagination.per_page
                            )"
                            :key="`investment-${investment.index}`"
                            :investment="investment"
                        />
                        <p v-if="!user_investments_active.length">{{ $t('account.investment_section.empty') }}</p>

                        <div
                            v-if="
                                user_investments_active_pagination.page * user_investments_active_pagination.per_page <
                                user_investments_active.length
                            "
