                                <span class="icon-arrow-right"></span>
                            </div>
                        </div>
                    </div>
                    <div v-if="active_tab === 'finished'">
                        <InvestmentCard
                            v-for="investment in user_investments_expired.slice(
                                0,
                                user_investments_expired_pagination.page * user_investments_expired_pagination.per_page
                            )"
                            :key="`investment-${investment.index}`"
                            :investment="investment"
                        />
                        <p v-if="!user_investments_expired.length">{{ $t('account.investment_section.empty') }}</p>

                        <div
                            v-if="
                                user_investments_expired_pagination.page *
                                    user_investments_expired_pagination.per_page <
                                user_investments_expired.length
                            "
                            class="load-more"
                            @click="user_investments_expired_pagination.page++"
                        >
                            <div class="wrap">
                                {{ $t('account.investment_section.show_more') }}
                                <span class="icon-arrow-right"></span>
                            </div>
                        </div>
                    </div>
                    <div v-if="active_tab === 'history'">
                        <InvestmentCard
                            v-for="investment in user_investments_history.slice(
                                0,
                                user_investments_history_pagination.page * user_investments_history_pagination.per_page
                            )"
                            :key="`investment-${investment.index}`"
                            :investment="investment"
                        />
                        <p v-if="!user_investments_history.length">{{ $t('account.investment_section.empty') }}</p>

                        <div
                            v-if="
                                user_investments_history_pagination.page *
                                    user_investments_history_pagination.per_page <
                                user_investments_history.length
                            "
                            class="load-more"
                            @click="user_investments_history_pagination.page++"
                        >
