                    <div class="community">
                        {{ $t('footer.community') }}
                        <div class="box">
                            <a href="#" target="_blank" rel="noopener noreferrer" class="tg">
                                <span class="icon-telegram icon"></span>
                            </a>
                            <a href="#" target="_blank" rel="noopener noreferrer" class="yt">
                                <span class="icon-youtube icon"></span>
                            </a>
                            <a href="#" target="_blank" rel="noopener noreferrer" class="mm">
