</template>

<script>
import vButton from '@/components/vButton.vue'
import KProgress from 'k-progress'

import LanguageSwitcher from '@/components/LanguageSwitcher.vue'
import InvestmentCard from '@/components/InvestmentCard.vue'
import vClipboard from '@/components/vClipboard'
import { sleep } from '@/helpers'

export default {
    name: 'Account',
    components: {
        KProgress,
        LanguageSwitcher,
        InvestmentCard,
        vClipboard,
        vButton,
    },
    data() {
        return {
            active_tab: 'active',
            user_investments_active_pagination: {
                page: 1,
                per_page: 10,
            },
            user_investments_expired_pagination: {
                page: 1,
                per_page: 10,
            },
            user_investments_history_pagination: {
                page: 1,
                per_page: 10,
            },
            loaders: {
                investments: true,
                form_investing: false,
                withdraw_profit: false,
            },
            invest_form: {
                amount: '0',
                referrer: '',
            },
            polls: {
                app_data: {
                    id: null,
                    interval: 2000,
                    hook: async () => {
                        await this.$store.dispatch('fetchUserInfo')
                        await sleep(500)
                        await this.$store.dispatch('fetchUserInvestments')
                    },
                },
            },
        }
    },
    watch: {
        '$web3.state.account': {
            immediate: true,
            async handler(n) {
                this.$store.commit('SET_USER_FIELD', ['address', n])

                if (n) {
                    await this.$store.dispatch('fetchUserInfo')

                    this.resetInvestForm()

                    await sleep(500)
                    await this.$store.dispatch('fetchUserInvestments')
                    this.loaders.investments = false

                    // Poll data
                    const poll_app_data = async () => {
                        clearInterval(this.polls.app_data.id)
                        await this.polls.app_data.hook()
                        this.polls.app_data.id = setInterval(poll_app_data, this.polls.app_data.interval)
                    }
                    await poll_app_data()
                }
            },
        },
        '$route.query.to': {
            immediate: true,
            handler(n) {
                this.$nextTick(() => {
                    this.scrollToBlock(n)
                    this.$nextTick(async () => {
                        try {
                            await this.$router.replace({ query: { ...this.$route.query, to: undefined } })
                        } catch (e) {
                            if (e.name !== 'NavigationDuplicated') {
                                throw e
                            }
                        }
                    })
                })
            },
        },
    },
    computed: {
        contract() {
            return this.$store.getters.contract
        },
        user() {
            return this.$store.getters.user
        },
        invitation_link() {
            if (!this.user.address) return ''
            const part = location.origin + '?ref='
            return part + this.user.address
        },
        user_investments() {
            return this.user.investments.slice().reverse()
        },
        user_investments_active() {
            return this.user_investments.filter((investment) => !investment.is_expired)
        },
        user_investments_expired() {
            return this.user_investments.filter((investment) => investment.is_expired && !investment.is_withdrawn)
        },
        user_investments_history() {
            return this.user_investments.filter((investment) => investment.is_expired && investment.is_withdrawn)
        },
        user_upgrade_progress_by_balance() {
            const user_level = this.user.level

            if (user_level < this.contract.settings.levels_count - 1) {
                const levelSettings = this.contract.level_settings[user_level + 1]
                const current = isNaN(this.user.withdraw_profit) ? 0 : this.user.withdraw_profit

                if (levelSettings) {
                    let percent = 0

                    if (levelSettings.upgrade_balance > 0) {
                        percent = (100 / levelSettings.upgrade_balance) * current
                    }

                    return {
                        current: current,
                        max: levelSettings.upgrade_balance,
                        percent: percent > 100 ? 100 : percent,
                    }
                }
            }

            return {
                current: 0,
                max: 0,
                percent: 0,
            }
        },
        user_active_investments_limit() {
            const user_level = this.user.level

            if (user_level < this.contract.settings.levels_count) {
                const levelSettings = this.contract.level_settings[user_level]

                if (levelSettings) return levelSettings.max_active_investments_count
            }

            return 0
        },

        invest_limits() {
            let user_level = isNaN(this.user.level) ? -1 : this.user.level

            if (user_level < this.contract.settings.levels_count) {
                const levelSettings = this.contract.level_settings[user_level]

                if (levelSettings)
                    return {
                        min: levelSettings.invest_amount_min,
                        max: levelSettings.invest_amount_max,
                    }
            }

            return false
        },
    },
    methods: {
        changeActiveTab(tab_name) {
            this.active_tab = tab_name
        },
        scrollToBlock(ref_name) {
            if (this.$refs[ref_name])
                window.scrollTo({
                    top: this.$refs[ref_name].offsetTop,
                    behavior: 'smooth',
                })
        },
        resetInvestForm() {
            this.invest_form.amount = this.invest_limits.max || '0'

            if (this.user.is_new) {
                this.invest_form.referrer = this.$root.getStorageReferrer() || ''
            } else {
                this.invest_form.referrer = this.user.invitor
            }
        },
        async submitInvestForm() {
            console.log('>> invest:', { ...this.invest_form })

            this.loaders.form_investing = true

            try {
                const receipt = await this.$web3.contract.methods
                    .invest(this.invest_form.referrer)
                    .send({
                        from: this.user.address,
                        value: this.$web3.library.utils.toWei('' + this.invest_form.amount), // value in wei
                    })
                    .on('error', (error, receipt) => {
                        console.log('> Invest error:', error, receipt)
                    })
                    .on('receipt', (receipt) => {
                        console.log('> Invest receipt:', receipt)
                        this.$root.makeSuccessToast('Invest', 'Successfully invested')
                    })

                console.log('>> Invest response:', receipt)
            } catch (error) {
                let error_msg = ''
                if (typeof error === 'string') {
                    error_msg = error
                } else {
                    error_msg = error.reason || error.message
                }

                console.error(`>>Invest error:`, error_msg)

                this.$root.makeErrorToast('Invest error', error_msg)
            }

            this.loaders.form_investing = false
            this.resetInvestForm()
        },
        async submitWithdrawProfit() {
            this.loaders.withdraw_profit = true

            try {
                const receipt = await this.$web3.contract.methods
                    .withdrawProfits()
                    .send({
                        from: this.user.address,
                    })

                    .on('error', (error, receipt) => {
                        console.log('> Withdraw error:', error, receipt)
                    })
                    .on('receipt', (receipt) => {
                        console.log('> Withdraw receipt:', receipt)
                        this.$root.makeSuccessToast('Withdraw profit', 'Successfully withdrawn')
                    })

                console.log('>> Withdraw response:', receipt)
            } catch (error) {
                let error_msg = ''
                if (typeof error === 'string') {
                    error_msg = error
                } else {
                    error_msg = error.reason || error.message
                }

                console.error(`>>Withdraw error:`, error_msg)

                this.$root.makeErrorToast('Invest error', error_msg)
            }

            this.loaders.withdraw_profit = false
        },
    },
}
</script>
