<template>
    <v-popover placement="bottom" trigger="manual" :open="is_open" offset="-1">
        <slot></slot>
        <template slot="popover">
            <slot name="success-text"> 
                {{ $t('_common.copied') }}
            </slot>
        </template>
    </v-popover>
</template>

<script>
import ClipboardJS from 'clipboard'
export default {
    name: 'VClipboard',
    props: {
        text: {
            type: String,
            required: true,
        },
    },
    data() {
        return {
            is_open: false,
            clipboard: null,
        }
    },
    mounted() {
        // clipboard
        this.clipboard = new ClipboardJS(this.$el, {
            text: () => {
                return this.text
            },
        })
        this.clipboard.on('success', (e) => {
            e.clearSelection()
            this.is_open = true

            setTimeout(() => {
                this.is_open = false
            }, 1000)
        })
        this.clipboard.on('error', function (e) {})
        // END clipboard
    },
    beforeDestroy() {
        this.clipboard.destroy()
    },
}
</script>
