import Vue from 'vue'
import formatCurrencyPlugin from 'format-currency'

const formatFiat = function (value, ...args) {
    if (!value) {
        value = 0
    }

    let options = {
        format: '%s %v',
        symbol: '$',
    }

    if (typeof args[0] === 'string') {
        options.symbol = args[0]
        if (typeof args[1] === 'object') options = Object.assign({}, options, args[1])
    }
    if (typeof args[0] === 'object') options = Object.assign({}, options, args[0])

    return formatCurrencyPlugin(value, options)
}

const formatCrypto = function (value, ...args) {
    if (!value) {
        value = 0
    }

    let options = {
        format: '%v %s',
        symbol: 'BTC',
        minFraction: 0,
        maxFraction: 18,
    }

    if (typeof args[0] === 'string') {
        options.symbol = args[0]
        if (typeof args[1] === 'object') options = Object.assign({}, options, args[1])
    }
    if (typeof args[0] === 'object') options = Object.assign({}, options, args[0])

    return formatCurrencyPlugin(value, options)
}

export default {
    install(_Vue) {
        _Vue.prototype.$formatFiat = formatFiat
        _Vue.prototype.$formatCrypto = formatCrypto

        _Vue.filter('formatFiat', formatFiat)
        _Vue.filter('formatCrypto', formatCrypto)
    },
}
