<template>
    <div class="investment-item" :class="investment_status_class">
        <div class="wrap">
            <div class="name">
                <span class="bold">{{ $t('investment_card.investment') }} #{{ investment.index + 1 }}</span>
                <div class="btn-status" :class="investment_status_class">
                    {{ investment.is_expired ? 'Finished' : 'Active' }}
                </div>
            </div>
            <div class="info">
                <div class="block">
                    {{ $t('investment_card.amount') }}
                    <span class="white medium">{{ $formatCrypto(investment.amount, 'BNB') }}</span>
                </div>
                <div class="block">
                    {{ $t('investment_card.earnings') }}
                    <span v-if="!investment.is_expired && !investment.is_withdrawn" class="white medium">{{
                        $formatCrypto(investment.available_dividends, {
                            symbol: 'BNB',
                            maxFraction: 6,
                        })
                    }}</span>
                    <span v-else class="white medium">{{
                        $formatCrypto(investment.total_dividends, {
                            symbol: 'BNB',
                            maxFraction: 6,
                        })
                    }}</span>
                </div>
                <div class="block">
                    {{ $t('investment_card.end_of_round') }}
                    <!--                    <span class="white medium">01 : 16 : 33 : 59</span>-->
                    <span class="white medium">{{ finish_in }}</span>
                </div>
            </div>
            <div v-if="investment.is_expired" class="btn-group">
                <v-button
                    @click.native="withdraw"
                    v-if="investment.is_expired && !investment.is_withdrawn"
                    className="silver ttu medium"
                    :isBusy="loaders.withdrawing || loaders.reinvesting"
                >
                    <span>{{ $t('_common.withdraw') }}</span>
                </v-button>
                <v-button
                    @click.native="reinvest"
                    v-if="investment.is_expired && !investment.is_withdrawn"
                    className="primary ttu medium"
                    :isBusy="loaders.reinvesting || loaders.withdrawing"
                >
                    <span>
                        {{ $t('_common.reinvest') }}
                    </span>
                </v-button>
            </div>
        </div>
        <KProgress
            v-if="!this.investment.is_expired"
            :percent="closing_progress_percent"
            :line-height="2"
            bg-color="#1D272C"
            color="#FFD61B"
            :border="false"
            :show-text="false"
        />
    </div>
</template>

<script>
import KProgress from 'k-progress'
import { duration as momentDuration } from 'moment'

import vButton from '@/components/vButton.vue'
export default {
    name: 'InvestmentCard',
    components: {
        KProgress,
        vButton,
    },
    props: {
        investment: {
            type: Object,
            required: true,
        },
    },
    data() {
        return {
            nonce: 0,
            nonce_interval: null,
            loaders: {
                reinvesting: false,
                withdrawing: false,
            },
        }
    },
    computed: {
        investment_status_class() {
            return this.investment.is_expired ? 'finished' : 'active'
        },
        finish_in() {
            this.nonce // To hack computed cache
            const diff = this.investment.expire_at * 1000 - Date.now()
            let seconds = 0
            let days = 0
            let hours = 0
            let minutes = 0

            if (diff > 0) {
                const expire_at = this.$root.moment(this.investment.expire_at * 1000)
                const duration = momentDuration(expire_at.diff(this.$root.moment()))

                seconds = parseInt(duration.asSeconds())

                days = parseInt(seconds / (24 * 3600))
                seconds -= days * 24 * 3600

                hours = parseInt(seconds / 3600)
                seconds -= hours * 3600

                minutes = parseInt(seconds / 60)
                seconds -= minutes * 60
            }

            return `${days < 10 ? '0' : ''}${days} : ${hours < 10 ? '0' : ''}${hours} : ${
                minutes < 10 ? '0' : ''
            }${minutes} : ${seconds < 10 ? '0' : ''}${seconds}`
        },
        closing_progress_percent() {
            let percent = 0
            percent = (100 / this.investment.total_dividends) * this.investment.available_dividends
            return percent > 100 ? 100 : percent
        },
    },
    created() {
        this.nonce_interval = setInterval(() => {
            this.nonce > 100 ? (this.nonce = 0) : this.nonce++
        }, 200)
    },
    methods: {
        async reinvest() {
            this.loaders.reinvesting = true

            try {
                const receipt = await this.$web3.contract.methods
                    .reinvest(this.investment.index)
                    .send({
                        from: this.$store.getters.user.address,
                    })
                    .on('error', (error) => {
                        console.error(`>>Investment #${this.investment.index + 1} Reinvest, on error:`, error)
                    })
                    .on('confirmation', (confirmationNumber, receipt) => {
                        if (confirmationNumber === 1) {
                            console.log(
                                `>>Investment #${this.investment.index + 1} reinvested`,
                                confirmationNumber,
                                receipt
                            )

                            this.$root.makeSuccessToast(
                                `${this.$t('investment_card.reinvest.success.title')}`,
                                `#${this.investment.index + 1} ${this.$t('investment_card.reinvest.success.text')}`
                            )
                        }
                    })

                console.log(`>>Investment #${this.investment.index + 1} Reinvest response:`, receipt)
            } catch (error) {
                let reinvest_error = ''
                if (typeof error === 'string') {
                    reinvest_error = error
                } else {
                    reinvest_error = error.reason || error.message
                }

                console.error(`>>Investment #${this.investment.index + 1} Reinvest error:`, reinvest_error)

                this.$root.makeErrorToast(`${this.$t('investment_card.reinvest.error.title')} #${this.investment.index + 1} ${this.$t('investment_card.reinvest.error.text')}`, reinvest_error)
            }

            this.loaders.reinvesting = false
        },
        async withdraw() {
            this.loaders.withdrawing = true

            try {
                const receipt = await this.$web3.contract.methods
                    .withdraw(this.investment.index)
                    .send({
                        from: this.$store.getters.user.address,
                    })
                    .on('error', (error) => {
                        console.error(`>>Investment #${this.investment.index + 1} Withdraw, on error:`, error)
                    })
                    .on('confirmation', (confirmationNumber, receipt) => {
                        if (confirmationNumber === 1) {
                            console.log(
                                `>>Investment #${this.investment.index + 1} withdrawn`,
                                confirmationNumber,
                                receipt
                            )

                            this.$root.makeSuccessToast(
                                `${this.$t('investment_card.withdraw.success.title')}`,
                                `#${this.investment.index + 1} ${this.$t('investment_card.withdraw.success.text')}`
                            )
                        }
                    })

                console.log(`>>Investment #${this.investment.index + 1} Withdraw response:`, receipt)
            } catch (error) {
                let withdraw_error = ''
                if (typeof error === 'string') {
                    withdraw_error = error
                } else {
                    withdraw_error = error.reason || error.message
                }

                console.error(`>>Investment #${this.investment.index + 1} Withdraw error:`, withdraw_error)

                this.$root.makeErrorToast(`${this.$t('investment_card.withdraw.error.title')} #${this.investment.index + 1} ${this.$t('investment_card.withdraw.error.text')}`, withdraw_error)
            }

            this.loaders.withdrawing = false
        },
    },
    beforeDestroy() {
        clearInterval(this.nonce_interval)
    },
}
</script>

<style></style>
