import { ConnectorNames } from '@/connectors'
import i18n from '@/plugins/i18n'

if (process.env.NODE_ENV === 'development') {
    window._DEBUG_ = true
}

// Fix of query and hash on uri when Router in hash mode
location.replace(location.pathname + location.hash + location.search)

import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import Toasted from 'vue-toasted'
import VTooltip from 'v-tooltip'
import Spinner from 'vue-simple-spinner/src/components/Spinner.vue'
import './assets/sass/components/toast.scss'
import FormatCurrencyPlugin from '@/plugins/format_currency'
import FormatNumberPlugin from '@/plugins/format_number'
import moment from 'moment'
import Cookies from 'js-cookie'

Vue.use(VTooltip, {
    defaultTrigger: 'click hover',
})

Vue.use(Toasted)

Vue.use(FormatCurrencyPlugin)
Vue.use(FormatNumberPlugin)
Vue.component('spinner', Spinner)

const appConfig = (Vue.prototype.$appConfig = require('@/../app.config'))
export const LOCAL_STORAGE_CONNECTOR_KEY = 'connector'

import Web3HttpProvider from 'web3-providers-http'
import VueWeb3 from '@/plugins/web3'
import { binanceChainListener } from '@/helpers'
const contractNetworkAppConfig = appConfig.contract.networks[appConfig.contract.currentNetwork]

Vue.use(VueWeb3, {
    web3: {
        handleRevert: true,
    },
    store,
    contract: contractNetworkAppConfig,
    fallbackProvider: new Web3HttpProvider(contractNetworkAppConfig.providers.http),
})

Vue.config.productionTip = false

new Vue({
    i18n,
    router,
    store,
    render: (h) => h(App),
    watch: {
        '$store.getters.is_logged_in'(n) {
            if (!n) {
                this.$router.replace({ name: 'Index' })
            }
        },
    },
    async created() {
        if (window._DEBUG_) window.app = this

        if (this.$route.query.ref && `${this.$route.query.ref}`.startsWith('0x')) {
            Cookies.set('ref', `${this.$route.query.ref}`, { expires: 365 })
            try {
                await this.$router.replace({ ...this.$route.query, ref: undefined })
            } catch (e) {
                if (e.name !== 'NavigationDuplicated') {
                    throw e
                }
            }
        }

        this.$store.commit('SET_CONTRACT_FIELD', [
            'address',
            this.$appConfig.contract.networks[this.$appConfig.contract.currentNetwork].address,
        ])

        this.eagerConnect()

        // Delay before use provider, wait till extension will inject provider on page
        setTimeout(async () => {
            await this.$store.dispatch('fetchContractData')
            await this.$store.dispatch('fetchContractLevelsSettings')
        })
    },
    methods: {
        moment() {
            return moment(...arguments)
        },
        eagerConnect() {
            const connectorId = window.localStorage.getItem(LOCAL_STORAGE_CONNECTOR_KEY)

            if (connectorId) {
                const isConnectorBinanceChain = connectorId === ConnectorNames.BSC
                const isBinanceChainDefined = typeof window.BinanceChain !== 'undefined'

                // Currently BSC extension doesn't always inject in time.
                // We must check to see if it exists, and if not, wait for it before proceeding.
                if (isConnectorBinanceChain && !isBinanceChainDefined) {
                    binanceChainListener().then(() => this.$store.dispatch('authLogin', connectorId))

                    return
                }

                this.$store.dispatch('authLogin', connectorId)
            }
        },
        makeToast() {
            alert('TODO makeToast:' + Array.from(arguments).join(' | '))
        },
        makeErrorToast(message, title) {
            this.$toasted.show(
                `<div class="toast-message">
                <div class="toast-title medium white">${title}</div>
                <span>${message}</span>
            </div>`,
                {
                    duration: 3000,
                    action: {
                        text: '',
                        onClick: (e, toastObject) => {
                            toastObject.goAway(0)
                        },
                        class: 'icon-close',
                    },
                    className: 'error-r',
                }
            )
        },
        makeSuccessToast(message, title) {
            this.$toasted.show(
                `<div class="toast-message">
                <div class="toast-title medium white">${title}</div>
                <span>${message}</span>
            </div>`,
                {
                    duration: 3000,
                    action: {
                        text: '',
                        onClick: (e, toastObject) => {
                            toastObject.goAway(0)
                        },
                        class: 'icon-close',
                    },
                    className: 'success-r',
                }
            )
        },
        getStorageReferrer() {
            return Cookies.get('ref') //|| this.$store.getters.contract.owner
        },
    },
}).$mount('#app')
