import Vue from 'vue'
import Vuex from 'vuex'
import { ConnectorsByName } from '@/connectors'
import { LOCAL_STORAGE_CONNECTOR_KEY } from '@/main'
import { getWeb3ErrorMessage } from '@/helpers'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        is_logged_in: false,
        contract: {
            address: '',
            owner: '',
            balance: undefined,
            total_investments_amount: undefined,
            total_withdrawals_amount: undefined,
            investors_count: '',
            settings: {
                levels_count: 0,
                investment_lifetime: 0,
            },
            level_settings: [],
        },
        user: {
            is_new: true,
            address: '',
            invitor: '',
            level: undefined,
            withdraw_profit: undefined,
            investments_count: undefined,
            reInvestments_count: undefined,
            invested_amount: undefined,
            reInvested_amount: undefined,
            investments: [],
        },
    },
    getters: {
        is_logged_in(state) {
            return state.is_logged_in
        },
        contract(state) {
            return state.contract
        },
        user(state) {
            return state.user
        },
    },
    mutations: {
        SET_IS_LOGGED_IN(state, value) {
            state.is_logged_in = value
        },

        SET_CONTRACT_FIELD_LOADING(state, [field, loading]) {
            state.contract[field].loading = loading
        },
        SET_CONTRACT_FIELD_VALUE(state, [field, value]) {
            state.contract[field].value = value
        },
        SET_CONTRACT_FIELD(state, [field, value]) {
            state.contract[field] = value
        },
        SET_USER_FIELD(state, [field, value]) {
            state.user[field] = value
        },
    },
    actions: {
        /*
         * Auth actions
         */
        async authLogin({ commit, dispatch }, connectorId) {
            const connector = ConnectorsByName[connectorId]

            return new Promise((resolve, reject) => {
                if (connector) {
                    this.$web3
                        .activate(
                            connector,
                            (error) => {
                                dispatch('authLogout')

                                reject({ title: 'Provider error', message: getWeb3ErrorMessage(error) })
                            },
                            false
                        )
                        .then((state) => {
                            if (state.account) {
                                window.localStorage.setItem(LOCAL_STORAGE_CONNECTOR_KEY, connectorId)

                                commit('SET_IS_LOGGED_IN', true)

                                resolve(true)
                            }
                        })
                } else {
                    reject({ title: 'Unable to find connector', message: 'The connector config is wrong' })
                }
            })
        },
        async authLogout({ commit }) {
            window.localStorage.removeItem(LOCAL_STORAGE_CONNECTOR_KEY)
            await this.$web3.deactivate()
            commit('SET_IS_LOGGED_IN', false)
        },

        /*
         * Contract actions
         */
        async fetchContractLevelsSettings({ commit, getters }) {
            const levels_count = getters.contract.settings.levels_count

            const level_settings = []
            for (let i = 0; i < levels_count; i++) {
                const data = await this.$web3.contract.methods.levelsSettings(i).call()
                level_settings.push({
                    invest_amount_min: Number(this.$web3.library.utils.fromWei(data.investAmountMin)),
                    invest_amount_max: Number(this.$web3.library.utils.fromWei(data.investAmountMax)),
                    investment_interest: Number(data.investmentInterest),
                    upgrade_balance: Number(this.$web3.library.utils.fromWei(data.upgradeBalance)),
                    max_active_investments_count: Number(data.maxActiveInvestmentsCount),
                })
            }

            commit('SET_CONTRACT_FIELD', ['level_settings', level_settings])
        },
        async fetchContractData({ commit }) {
            await Promise.all([
                new Promise((resolve) => {
                    this.$web3.contract.methods
                        .owner()
                        .call()
                        .then((value) => {
                            commit('SET_CONTRACT_FIELD', ['owner', value])

                            resolve()
                        })
                }),
                new Promise((resolve) => {
                    this.$web3.contract.methods
                        .contractBalance()
                        .call()
                        .then((value) => {
                            commit('SET_CONTRACT_FIELD', ['balance', Number(this.$web3.library.utils.fromWei(value))])

                            resolve()
                        })
                }),
                new Promise((resolve) => {
                    this.$web3.contract.methods
                        .statistic()
                        .call()
                        .then((statistics) => {
                            commit('SET_CONTRACT_FIELD', [
                                'total_investments_amount',
                                Number(this.$web3.library.utils.fromWei(statistics.investedAmount)),
                            ])
                            commit('SET_CONTRACT_FIELD', [
                                'total_withdrawals_amount',
                                Number(this.$web3.library.utils.fromWei(statistics.withdrawalsAmount)),
                            ])
                            commit('SET_CONTRACT_FIELD', ['investors_count', Number(statistics.investorsCount)])

                            resolve()
                        })
                }),
                new Promise((resolve) => {
                    this.$web3.contract.methods
                        .settings()
                        .call()
                        .then((settings) => {
                            commit('SET_CONTRACT_FIELD', [
                                'settings',
                                {
                                    levels_count: Number(settings.levelsCount),
                                    investment_lifetime: Number(settings.investmentLifetime),
                                },
                            ])

                            resolve()
                        })
                }),
            ])
        },

        /*
         * User actions
         */
        async fetchUserInfo({ commit, getters }) {
            await Promise.all([
                new Promise((resolve) => {
                    this.$web3.contract.methods
                        .investorInfoByAddress(getters.user.address)
                        .call({ from: getters.user.address })
                        .then((investor_info) => {
                            commit('SET_USER_FIELD', ['invitor', investor_info[0]])
                            commit('SET_USER_FIELD', [
                                'withdraw_profit',
                                Number(this.$web3.library.utils.fromWei(investor_info[1])),
                            ])

                            commit('SET_USER_FIELD', [
                                'is_new',
                                /^0x0{40}$/.test(getters.user.invitor) &&
                                    getters.contract.owner.toLowerCase() !== getters.user.address.toLowerCase(),
                            ])

                            resolve()
                        })
                }),
                new Promise((resolve) => {
                    this.$web3.contract.methods
                        .investorLevelByAddress(getters.user.address)
                        .call({ from: getters.user.address })
                        .then((value) => {
                            commit('SET_USER_FIELD', ['level', Number(value)])

                            resolve()
                        })
                }),
                new Promise((resolve) => {
                    this.$web3.contract.methods
                        .investorStatsByAddress(getters.user.address)
                        .call({ from: getters.user.address })
                        .then((investor_stats) => {
                            commit('SET_USER_FIELD', ['investments_count', Number(investor_stats[0])])
                            commit('SET_USER_FIELD', ['reInvestments_count', Number(investor_stats[1])])
                            commit('SET_USER_FIELD', [
                                'invested_amount',
                                Number(this.$web3.library.utils.fromWei(investor_stats[2])),
                            ])
                            commit('SET_USER_FIELD', [
                                'reInvested_amount',
                                Number(this.$web3.library.utils.fromWei(investor_stats[3])),
                            ])

                            resolve()
                        })
                }),
            ])
        },
        async fetchUserInvestments({ commit, getters }) {
            const investments_data = [
                await this.$web3.contract.methods
                    .investmentsCreatedAtByAddress(getters.user.address)
                    .call({ from: getters.user.address }),
                await this.$web3.contract.methods
                    .investmentsAmountByAddress(getters.user.address)
                    .call({ from: getters.user.address }),
                await this.$web3.contract.methods
                    .investmentsInvestorLevelByAddress(getters.user.address)
                    .call({ from: getters.user.address }),
                await this.$web3.contract.methods
                    .investmentsIsExpiredByAddress(getters.user.address)
                    .call({ from: getters.user.address }),
                await this.$web3.contract.methods
                    .investmentsIsWithdrawnByAddress(getters.user.address)
                    .call({ from: getters.user.address }),
                await this.$web3.contract.methods
                    .investmentsTotalDividendByAddress(getters.user.address)
                    .call({ from: getters.user.address }),
                await this.$web3.contract.methods
                    .investmentsAvailableDividendsByAddress(getters.user.address)
                    .call({ from: getters.user.address }),
            ]

            const investments_count = getters.user.investments_count
            const investments = []

            for (let i = 0; i < investments_count; i++) {
                const investment = {
                    index: i,
                    created_at: Number(investments_data[0][i]),
                    expire_at: Number(investments_data[0][i]),
                    amount: Number(this.$web3.library.utils.fromWei(investments_data[1][i])),
                    level: Number(investments_data[2][i]),
                    is_expired: !!Number(investments_data[3][i]),
                    is_withdrawn: !!Number(investments_data[4][i]),
                    total_dividends: Number(this.$web3.library.utils.fromWei(investments_data[5][i])),
                    available_dividends: Number(this.$web3.library.utils.fromWei(investments_data[6][i])),
                }

                investment.expire_at = investment.created_at + getters.contract.settings.investment_lifetime

                investments.push(investment)
            }

            commit('SET_USER_FIELD', ['investments', investments])
        },
    },
})
